<template>
	<div class="login">
		<h1>用户登录</h1>
		<button @click="login()">登陆</button>
	</div>
</template>

<script>
	export default{
		methods:{
			login() {
				var params = {
					username:'201740533152',
					password:'123456'
				};
				this.$http.post(this.$host + '/user/login', params)
					.then(response => {
						var result = response.data
						if(result.status == 200) {
							this.$store.dispatch('login',{username:result.data.username})
							this.$router.push('/center')
						}else{
							alert(result.message)
						}
					}).catch(error => {
						console.log(error)
					})
				
				
				
				// console.log('login')
				// window.sessionStorage.setItem('username','zhangsan');
				// this.$store.dispatch('login',{username: 'lisi'})
				// this.$router.push('/center')
			}
		}
	}
</script>

