export default {
	state: {
		username:window.sessionStorage.getItem('username') ? window.sessionStorage.getItem('username') : null
	},
	getters:{
		username(state) {
			return state.username
		}
	},
	mutations: {
		LOGIN(state,params) {
			state.username = params.username
			window.sessionStorage.setItem('username',params.username)
		},
		LOGOUT(state) {
			state.username = null
			window.sessionStorage.removeItem('username')
		}
	},
	actions: {
		login(context,params) {
			context.commit('LOGIN',params)
		},
		logout(context) {
			context.commit('LOGOUT')
		}
	}		
}