<template>
	<div class="center">
		<h1>个人中心</h1>
		欢迎您 {{username}}
		<button @click="logout()">退出</button>
	</div>
</template>

<script>
	export default {
		computed: {
			username() {
				return this.$store.getters.username
			}

		},
		methods:{
			logout() {
				this.$store.dispatch('logout')
				this.$router.push('/login')
			}
		}
	}
</script>

